let SwitchWidget = require("SwitchWidget");
let LayerCamerasMove = require("LayerCamerasMove");

let UIManager = cc.Class({
    extends: cc.Component,

    properties: {
        isHActive: true,

        hNode: cc.Node,
        vNOde: cc.Node,
        uiCamera: cc.Camera,

        musicWidget: cc.Widget,
        musicButton: cc.Node,

        startBlock: cc.Node,

        switchwidget: SwitchWidget,
        game: null,
        cameraMove: LayerCamerasMove
    },

    onLoad: function(){
        this.startBlock.active = true;


        this.node.on("mobileView", this.mobileView.bind(this));
        this.node.on("pcView", this.pcView.bind(this));

        if (this.switchwidget && !this.switchwidget.isPcView) {
            this.mobileView(this.switchwidget.fw);

        }
    },


    mobileView(){
       
        this.isHActive = false;
        this.hNode.active = false;
        this.vNOde.active = true;

        this.musicWidget.bottom = 70;
        this.musicWidget.left = -5;

        this.musicButton.scale = 0.6;
        this.musicButton.x = 20;
       
        let ws = this.node.getComponentsInChildren(cc.Widget);
        for(let i in ws){
            ws[i].updateAlignment();
        }
       
        this.vNOde.getComponent('UIBase').sendButton.active = this.game.sendButton.active;
        this.vNOde.getComponent('UIBase').text.string = this.game.editBox.string;

        this.game.sendButton = this.vNOde.getComponent('UIBase').sendButton;
        this.game.editBox = this.vNOde.getComponent('UIBase').text;
    },

    pcView(){
       
        this.isHActive = true;
        this.hNode.active = true;
        this.vNOde.active = false;

        this.musicWidget.bottom = 46;
        this.musicWidget.left = 62;
        this.musicWidget.updateAlignment();

        this.musicButton.scale = 1;
        this.musicButton.x = 0;

        let ws = this.node.getComponentsInChildren(cc.Widget);
        for(let i in ws){
            ws[i].updateAlignment();
        }

        this.hNode.getComponent('UIBase').sendButton.active = this.game.sendButton.active;
        this.hNode.getComponent('UIBase').text.string = this.game.editBox.string;

        this.game.sendButton = this.hNode.getComponent('UIBase').sendButton;
        this.game.editBox = this.hNode.getComponent('UIBase').text;
       
    },

    closeStartBlock() {
        this.startBlock.active = false;
        this.cameraMove.initialize();
        this.game.blockClickHandler();
    },

    isBlockActive(){
       return this.startBlock.active ;
    },

    updateSendButton(active){
        if(!this.isHActive){
            this.vNOde.updateSendButton(active)
        }
    }
});